.dsm-viewer {
  .select-label {
    display: block;
    font-size: 13px;
    font-weight: 700;
    margin-bottom: 4px;
    color: #c3c3c3;
  }
  .form-control {
    display: inline-block;
    width: 220px;
    margin-right: 16px;
    .ant-select {
      width: 100%;
    }
  }
  .selects {
    background: #fafafa;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    padding: 12px 24px 16px 24px;
    margin-bottom: 16px;
    border-bottom: 1px solid #efefef;
  }
  .dsm-viewer-body {
    position: absolute;
    top: 98px;
    bottom: 0px;
    left: 24px;
    right: 24px;
  }
  .ingestion-date-icon {
    font-size: 22px;
    position: relative;
    top: 3px;
    margin-right: 3px;
  }
}

// Dark theme related styles

.dsm-viewer-body {
  margin-top: 20px;
}
.app-root,
.global-layout {
  min-height: 100vh !important;
  height: auto !important;
  background: #000;
}
.global-content,
body {
  background-color: #000;
}
.ant-card {
  margin-bottom: 16px !important;
}

.ant-card.ant-card-bordered {
  background-color: #131313;
  border: none;
  color: #a5a5a5;
  .ant-card-head {
    border-color: #434343;
    color: #c3c3c3;
    font-weight: 500;
    font-size: 17px;
  }
  .label {
    font-weight: 600;
    display: inline-block;
    margin-right: 10px;
    color: rgba(255, 255, 255, 0.68);
  }
  p {
    margin-bottom: 3px;
  }
}
.ant-layout-header.global-header {
  background-color: rgb(19, 19, 19);
  border-bottom: 1px solid #434343;
}
.ant-layout-header.global-header .ant-menu-dark {
  background-color: transparent;
}
.ant-layout-header.global-header .ant-menu.ant-menu-dark .ant-menu-item-selected {
  background-color: #434343;
  a {
    color: #fff;
  }
}
.list-items {
  .ant-card.ant-card-bordered {
    width: 960px;
    margin: auto;
  }
  p {
    display: inline-block;
    min-width: 100px;
    margin: 10px 20px !important;
  }
  .label {
    display: block !important;
    margin-bottom: 8px;
  }
  .ant-card-head-title {
    padding: 12px 20px !important;
    cursor: pointer;
  }
}

.ag-theme-balham .ag-header {
  background-color: #131313 !important;
  color: rgba(255, 255, 255, 0.68) !important;
  border-color: #434343 !important;
}
.ag-theme-balham,
.ag-theme-balham .ag-row {
  background-color: #131313 !important;
  color: rgba(255, 255, 255, 0.68) !important;
}
.ag-theme-balham .ag-root,
.ag-theme-balham .ag-header,
.ag-theme-balham .ag-header-row,
.ag-theme-balham .ag-paging-panel,
.ag-theme-balham .ag-row {
  border-color: #434343 !important;
}
.ag-theme-balham .ag-paging-panel {
  color: rgba(255, 255, 255, 0.68) !important;
}
.ag-paging-page-summary-panel button {
  background-color: #ccc !important;
}

.ant-select-dropdown-menu-item {
  font-weight: 300;
  color: rgba(255, 255, 255, 0.52) !important;
}

.ag-theme-balham .ag-floating-filter-body input,
.ant-select-selection,
.ant-select-dropdown,
.ant-select-dropdown-menu-item-selected {
  color: rgba(255, 255, 255, 1) !important;
  background: #3c3c3c !important;
  border-color: #434343 !important;
  .ant-select-arrow {
    color: #ccc !important;
  }
}
.search-input-wrapper .search-input.ant-input-affix-wrapper .ant-input:not(:first-child) {
  color: rgb(206, 206, 206) !important;
  background: rgba(19, 19, 19, 0.85) !important;
  border-color: #434343 !important;
  font-weight: 200;
  letter-spacing: 1.2px;
}
.search-input-wrapper {
  .anticon {
    color: rgb(130, 127, 127) !important;
  }
}
.dsm-viewer {
  .selects {
    background: #131313;
    position: relative;
    top: inherit;
    left: inherit;
    right: inherit;
    padding: 12px 24px 16px 24px;
    margin-bottom: 16px;
    border-bottom: 0;
  }
  .dsm-viewer-body {
    position: relative;
    top: inherit;
    bottom: inherit;
    left: inherit;
    right: inherit;
  }
}
.global-content.global-content-search {
  background: #000 !important;
}
.search-results {
  position: relative;
  top: 20px;
}
.ag-theme-balham .ag-overlay-loading-wrapper {
  background-color: rgba(0, 0, 0, 0.7) !important;
  .ant-spin-dot-item {
    background-color: #fff;
  }
}

.search-select-options {
  width: 100%;
  .ant-select-selection {
    color: #cecece !important;
    background: rgba(19, 19, 19, 0.85) !important;
    border-color: #434343 !important;
  }
  .ant-select-selection--single {
    height: auto !important;
  }
  .ant-select-selection__rendered {
    line-height: 53px;
    font-size: 20px;
    font-weight: 200;
    letter-spacing: 1px;
  }
}

.initial-device-search-done {
  .search-select-options {
    .ant-select-selection--single {
      height: auto !important;
    }
    .ant-select-selection__rendered {
      line-height: 38px;
      font-size: 16px;
      font-weight: 200;
      letter-spacing: 1px;
    }
  }
}
