$icons-path: '~ag-grid-community/src/styles/balham-icons/';
$border-color: #e8e8e8;
$header-background-color: #fafafa;
$font-size: 14px;
$header-icon-size: 14px;
$secondary-font-size: 14px;
$grid-size: 5px; // N.B. Have to adjust `headerHeight` and `rowHeight` in `<AgGridReact>`if change this value, and vica-versa
$hover-color: #e6f7ff;

@import '~ag-grid-community/src/styles/ag-grid.scss';
@import '~ag-grid-community/src/styles/ag-theme-balham.scss';

.ag-theme-balham {
  color: rgba(0, 0, 0, 0.65);
  .ag-font-style {
    user-select: initial;
    -moz-user-select: text;
    -ms-user-select: text;
    -webkit-user-select: text;
  }
  .ag-header {
    color: rgba(0, 0, 0, 0.65);
    .ant-select-selection-selected-value {
      font-weight: normal;
    }
  }
  .ag-floating-filter-body input {
    height: 24px;
    margin: 0;
    width: 100%;
  }
  .ant-select-auto-complete.ant-select .ant-input,
  .ag-floating-filter-body input,
  input[type='text'],
  input[type='date'] {
    border: 1px solid #d9d9d9;
    padding: 1px 7px;
  }
  .ant-select .ant-select-search__field {
    border: 0;
    padding: 1px 0;
  }
  .ag-filter-body-wrapper {
    padding: 10px 10px 0 10px;
    input[type='radio'],
    input[type='text'],
    input[type='date'] {
      margin-bottom: 10px;
    }
    input[type='radio'] {
      margin-bottom: 8px;
    }
    .ag-filter-select {
      margin-top: 0;
      margin-bottom: 10px;
      border: 1px solid #d9d9d9;
    }
  }
  .ag-header-cell-label,
  .ag-floating-filter-button button,
  .ag-paging-button {
    cursor: pointer;
  }
  .ag-menu {
    z-index: 1001;
  }
  .ag-paging-panel {
    height: 30px;
  }
  // This fixes the issue with misaligned pagination controls (in the modal) in Firefox
  button[ref='btFirst'],
  button[ref='btPrevious'],
  button[ref='btNext'],
  button[ref='btLast'] {
    text-indent: -9999px;
  }
  .ag-icon-filter,
  .ag-icon-group {
    background-color: rgba(156, 156, 156, 0.5) !important;
  }
}

// Ant-d Table and pagination related styles.

.react-resizable {
  position: relative;
  background-clip: padding-box;
}
.react-resizable-handle {
  position: absolute;
  width: 10px;
  height: 100%;
  bottom: 0;
  right: -5px;
  cursor: col-resize;
  z-index: 1;
}

.ant-pagination {
  margin-top: 10px;
  text-align: right;
  .ant-pagination-prev .ant-pagination-item-link,
  .ant-pagination-next .ant-pagination-item-link {
    background-color: #191919;
    border: 1px solid #434343;
    color: #fff;
    border-radius: 0;
  }
  .ant-pagination-item {
    background-color: #191919;
    border: 1px solid #434343;
    color: #fff;
    border-radius: 0;
    a {
      color: rgba(247, 247, 247, 0.62);
    }
  }
  .ant-pagination-item-active {
    a {
      color: #fff;
      background: #434343;
    }
  }
  .ant-pagination-disabled {
    opacity: 0.6;
  }
  .ant-pagination-item:focus,
  .ant-pagination-item:hover {
    border-color: #434343;
    a {
      color: #fff;
      background: #434343;
    }
  }
  .ant-pagination-jump-prev .ant-pagination-item-container .ant-pagination-item-ellipsis,
  .ant-pagination-jump-next .ant-pagination-item-container .ant-pagination-item-ellipsis {
    color: rgba(247, 247, 247, 0.62);
  }
  .ant-pagination-jump-prev .ant-pagination-item-container .ant-pagination-item-link-icon,
  .ant-pagination-jump-next .ant-pagination-item-container .ant-pagination-item-link-icon {
    color: rgba(247, 247, 247, 0.62);
  }
}
.data-table-wrapper {
  .ant-table table {
    color: rgba(255, 255, 255, 0.72);
    background: #191919;
  }
  .ant-table-thead > tr > th,
  .ant-table-header {
    background: #272727;
    color: rgba(255, 255, 255, 0.72);
    border-bottom: 1px solid #434343;
    word-break: break-all;
    font-size: 13px;
    // padding-top: 3px;
    // padding-bottom: 7px;
    &.value {
      color: #fff;
    }
  }
  .ant-table-tbody > tr > td {
    border-bottom: 1px solid #434343;
    height: 40px;
    overflow: hidden;
    white-space: nowrap;
    &.value {
      color: #fff;
    }
    .wrap-single-line {
      height: 20px;
      overflow: hidden;
    }
  }
  .ant-table-placeholder {
    color: rgba(255, 255, 255, 0.72);
    background: #191919;
    border-color: #434343;
  }
  .ant-table-tbody > tr:hover:not(.ant-table-expanded-row):not(.ant-table-row-selected) > td {
    background: rgba(67, 67, 67, 0.6);
  }
  .ant-table-thead > tr > th.ant-table-column-has-actions.ant-table-column-has-sorters:hover,
  .ant-table-thead > tr > th.ant-table-column-sort {
    background: #404040;
  }
  .ant-table-thead
    > tr
    > th
    .ant-table-column-sorter
    .ant-table-column-sorter-inner
    .ant-table-column-sorter-down.on,
  .ant-table-thead
    > tr
    > th
    .ant-table-column-sorter
    .ant-table-column-sorter-inner
    .ant-table-column-sorter-up.on {
    color: #636363;
  }
  .ant-table-fixed-header > .ant-table-content > .ant-table-scroll > .ant-table-body {
    position: relative;
    background: #5d5d5d;
  }
  .ant-table-thead > tr > th,
  .ant-table-tbody > tr > td {
    padding: 10px 5px;
  }
  .ant-table-thead > tr:first-child > th:first-child,
  .ant-table-tbody > tr > td:first-child {
    padding-left: 12px;
  }
  // .ant-table-header.ant-table-hide-scrollbar {
  //   overflow-x: auto !important;
  //   overflow-y: hidden !important;
  // }
}
.ant-empty-description {
  color: #fff;
}
.ant-spin-blur {
  opacity: 0.32;
}
.ant-table-scroll table {
  width: 100% !important;
  min-width: 100% !important;
  table-layout: fixed !important;
}

.ant-table-thead
  > tr
  > th.ant-table-column-has-actions.ant-table-column-has-sorters:hover
  .anticon-filter,
.ant-table-thead
  > tr
  > th.ant-table-column-has-actions.ant-table-column-has-sorters:hover
  .ant-table-filter-icon {
  background: #404040 !important;
}
.ant-table-thead
  > tr
  > th.ant-table-column-has-actions.ant-table-column-has-filters
  .anticon-filter.ant-table-filter-open,
.ant-table-thead
  > tr
  > th.ant-table-column-has-actions.ant-table-column-has-filters
  .ant-table-filter-icon.ant-table-filter-open {
  color: rgba(0, 0, 0, 0.45);
  background: #404040 !important;
}
.ant-table-filter-dropdown {
  background: #404040 !important;
  .ant-input {
    color: #f1f1f1;
    background-color: #191919;
    border: 1px solid #434343;
    &:hover {
      border: 1px solid #434343;
    }
    &:focus {
      box-shadow: none;
    }
  }
  .ant-btn {
    font-size: 10px;
  }
  .ant-dropdown-menu-without-submenu {
    background-color: transparent;
  }
}

.ant-table-filter-dropdown-link {
  background: #000;
  border: 1px solid #000;
  min-width: 90px;
  text-align: center;
  color: #cecece !important;
  font-size: 10px;
  height: 20px;
  line-height: 18px;
  margin: 0 2px;
}
.ant-dropdown-menu-item,
.ant-dropdown-menu-submenu-title {
  color: rgba(255, 255, 255, 0.85);
}
.ant-table-filter-dropdown-btns {
  border-top: 1px solid #272727;
}

.ant-dropdown-menu-item {
  .ant-checkbox-inner {
    background-color: #404040;
    border: 1px solid #8a8a8a;
  }
  &:hover,
  &.ant-dropdown-menu-item-selected,
  &.ant-dropdown-menu-item-active {
    color: rgba(255, 255, 255, 0.85);
    background-color: #313131 !important;
  }
  .ant-checkbox-checked .ant-checkbox-inner {
    background-color: #323435;
    border-color: #cbccce;
  }
}

.data-table-wrapper {
  .ant-table-thead th {
    .ant-table-header-column {
      width: 100%;
      .ant-table-column-sorters {
        position: relative;
        width: 100%;
      }
      .ant-table-column-sorter {
        position: absolute;
        top: 2px;
        right: 0px;
        width: 20px;
      }
      .ant-input {
        font-size: 12px;
        padding: 0px 7px;
        margin-top: 5px;
        height: 26px;
        background: #191919;
        border: 1px solid #6b6a6a;
        color: #f1f1f1;
        &:focus {
          box-shadow: none;
        }
      }

      .ant-select {
        width: 100%;
        font-size: 11px;
        margin-top: 3px;
        .ant-select-selection {
          height: 26px;
          background: #191919 !important;
          border-color: #6b6a6a !important;
          .ant-select-selection__rendered {
            line-height: 22px;
            margin-left: 5px;
            margin-right: 5px;
          }
          .ant-select-arrow {
            font-size: 10px;
          }
          &:focus {
            box-shadow: none;
          }
        }
      }
    }
  }
}
.table-filter-dropdown {
  &.ant-select-dropdown {
    font-size: 11px;
    .ant-select-selection,
    .ant-select-dropdown,
    .ant-select-dropdown-menu-item-selected,
    .ant-select-dropdown-menu-item-active {
      background: #6b6969 !important;
    }
    .ant-select-dropdown-menu-item {
      &:hover:not(.ant-select-dropdown-menu-item-disabled) {
        background: #6b6969 !important;
      }
    }
  }
}
.custom-hd-wrp {
  // max-width: 200px;
  .ant-select-selection .ant-select-selection-selected-value {
    color: #b9b9b9 !important;
  }
  .ant-select-auto-complete {
    width: 100% !important;
    .ant-select-selection__rendered {
      margin: 0 !important;
    }
  }
}
